import React from 'react'
import { Link } from 'react-router-dom'

const blogList = ({ blogs }) => {
    return (
        <>
            {blogs.map((blog, key) => {
                return <Link key={key} to={`/single/${blog.name}`}>
                    <h2>{blog.title}</h2>
                    {blog.content}
                </Link>
            })}
        </>
    )
}

export default blogList
